.App {
  background-color: #2a1ea6;
  background-image: linear-gradient(to right top, #2a1ea6, #313db6, #3e57c5, #5070d1, #6588db, #619be7, #61adf0, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
  min-height: 100vh;
  color: white;
  font-family: 'Titillium Web', sans-serif;
}

header h1{
  margin: 0;
  padding: 15px 25px;
  color: white;
}

.content{
  display: flex;
}
.col-item{
  flex: 1;
}

.buttons-convert{
  height: 50px;
}

.buttons-convert label{
  padding: 15px;
  font-weight: bold;
  font-size: 1.4em;
}

.buttons-convert .inp-prefix{
  margin: 0 0 0 15px; 
  font-size: 1.2em;
  border: none;
  padding: 5px 15px;
  border-radius: 5px;
}

#editor-left{
  width: 99% !important;
}

#editor-right{
  width: 99% !important;
  float:right;
}

.editor-right,
.editor-left{
  flex: 4 2 200px;
}

.item-colum{
  top: 40%;
  text-align: center;
  width: 100%;
  position: relative;
}

.item-colum svg{
  font-size: 3rem;
}

.item-row{
  display: none;
}

footer div{
  height: 50px;
}

.g-msc{
  display: block;
  text-align: center;
  text-decoration: none;
  padding-top: 10px;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
}

.label-switch{
  font-weight: bold;
  font-size: 1.4em;
  display: flex;
  justify-content: space-between;
  line-height: 1.5rem;
  padding-top: 0.1rem;
}

.button-change{
  border: none;
  flex: 1;
  background:rgb(175, 175, 238);
  background: linear-gradient(36deg, rgb(175, 175, 238) 0%, rgb(171, 210, 217) 100%);
  color: white;
  padding: 5px 5px 5px 10px;
  line-height: 1rem;
  font-size: 1rem;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.5);
}

.button-change:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.button-change:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.button-change.active{
  background: rgb(97,97,210);
background: linear-gradient(36deg, rgba(97,97,210,1) 0%, rgba(0,212,255,1) 100%);
  color: white;
}

.button-change span.label-exemple{
  font-size: 1.7rem;
  margin-right: 5px;
}

@media only screen and (max-width: 1000px) {
  .inp-prefix{
    width: 50%;
  }
}

@media only screen and (max-width: 700px) {
  header h1{
    text-align: center;
    font-size: 1.8em;
  }
  .content{
    flex-direction: column;
  }
  
  .buttons-convert{
    height: unset;
    text-align: center;
  }
  
  .buttons-convert .col-item{
    padding: 15px 0;
  }

  .label-switch{
    position: unset;
  }

  #editor-left{
    width: 100% !important;
    height: 300px !important;
  }

  #editor-right{
    width: 100% !important;
    float:none;
    height: 300px !important;
  }



  .item-colum{
    display: none;
  }

  .item-row{
    display: block;
  }

  .item-row svg{
    width: 100%;
    font-size: 3rem;
  }
}